/*
 * AdaptersPage Messages
 *
 * This contains all the text for the AdaptersPage component.
 */
import { defineMessages } from 'react-intl';

const mess = defineMessages({
  addAdapter: {
    id: 'app.containers.AdaptersPage.addAdapter',
    defaultMessage: 'Add Adapter',
  },
  nameField: {
    id: 'app.containers.AdaptersPage.nameField',
    defaultMessage: 'Name',
  },
  zeroStateInfo: {
    id: 'app.containers.AdaptersPage.zeroStateInfo',
    defaultMessage:
      'An Adapter is a custom software component, deployed on gateway devices, whose purpose is to serve as a proxy between the ClearBlade platform and IoT devices that cannot directly communicate with the ClearBlade Platform',
  },
});

export default mess;



