/*
 *
 * AdaptersPage
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';
import { FormattedMessage } from 'react-intl';
import { compose } from 'redux';
import injectReducer from 'utils/injectReducer';
import { selectShowAdapterInfoModal } from 'containers/Console/selectors/ui';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import messages from 'containers/AdaptersPage/messages';
import ConsolePage from 'components/ConsolePage';
import PageHeader from 'components/ConsolePageHeader';
import CardTable from 'components/CardTableForEntities';
import consoleMessages from 'containers/Console/messages';
import { Button } from 'reactstrap';
import { AddIcon, AdapterIcon } from 'components/EntityIcons';
import AdapterCard from 'components/AdapterCard';
import { adapterSelectors, adapterActions } from '../Console/ducks';
import { BaseAdapterModel } from 'cb-utils/console-entity-models';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';

import AdapterInfoModal from 'components/AdapterInfoModal';
import {
  createOnAddNewAdapter,
  createDismissAdapterInfoModal,
} from 'containers/Console/actions/ui';
import { CBDispatch } from 'cb-utils/typeUtils';
import LearnMoreLink from 'components/LearnMoreLink';
import { createStructuredSelector } from 'reselect';

const defaultSortKey = 'name';

interface AdaptersPageProps extends SystemRouteProps<{}> {}

const createColumnsForCardTable = () => {
  return [
    {
      component: (
        <b>
          <FormattedMessage {...messages.nameField} />
        </b>
      ),
      isSortable: true,
      sortKey: defaultSortKey,
    },
  ];
};

export class AdaptersPage extends React.PureComponent<
  AdaptersPageProps & StateToProps & DispatchToProps,
  {}
> {
  componentWillMount() {
    this.props.init(this.props.match.params.systemKey);
  }

  render() {
    const {
      adapters,
      showAdapterInfoModal,
      dismissAdapterInfoModal,
      createNewAdapter,
      isCreatingNewAdapter,
    } = this.props;
    return (
      <React.Fragment>
        <Helmet
          title="ClearBlade - Adapters"
          meta={[
            { name: 'description', content: 'Description of AdaptersPage' },
          ]}
        />
        {showAdapterInfoModal && (
          <AdapterInfoModal
            toggle={dismissAdapterInfoModal}
            submit={(adapter: BaseAdapterModel) => createNewAdapter(adapter)}
            isSubmitting={isCreatingNewAdapter}
            error={''}
          />
        )}
        <ConsolePage
          subheader={this.renderSubHeader()}
          isFetchingAssets={this.props.isFetching}
          zeroState={{
            show: this.props.totalAdapters === 0,
            title: consoleMessages.assetClassAdapters,
            info: messages.zeroStateInfo,
            moreInfo: (
              <LearnMoreLink url="https://docs.clearblade.com/v/3/1-platform_concepts/Adapters/" />
            ),
            icon: <AdapterIcon />,
            addButton: this.renderAddButton({ size: 'lg' }),
          }}
        >
          <CardTable
            defaultSortKey={defaultSortKey}
            headerColumns={createColumnsForCardTable()}
            entities={adapters}
            entityCard={AdapterCard}
            cardProp="adapter"
          />
        </ConsolePage>
      </React.Fragment>
    );
  }

  renderAddButton = ({ size = 'md' }) => (
    <Button
      size={size}
      color="primary"
      data-cy="add-adapter"
      onClick={this.props.onAddNewAdapter}
    >
      <AddIcon />
      &nbsp;
      <FormattedMessage {...messages.addAdapter} />
    </Button>
  );

  renderSubHeader = () => {
    return (
      <React.Fragment>
        <PageHeader
          header={
            <div>
              <AdapterIcon />{' '}
              <FormattedMessage {...consoleMessages.assetClassAdapters} />
            </div>
          }
          actionButton={this.renderAddButton}
        />
      </React.Fragment>
    );
  };
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  adapters: adapterSelectors.wholeSlice,
  isFetching: adapterSelectors.fetchAll.isLoading,
  totalAdapters: adapterSelectors.entitiesCount,
  showAdapterInfoModal: selectShowAdapterInfoModal(),
  isCreatingNewAdapter: adapterSelectors.create.isLoading,
});

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;
function mapDispatchToProps(dispatch: CBDispatch, ownProps: AdaptersPageProps) {
  return {
    init: (key: string) => dispatch(adapterActions.fetchAll.request(key)),
    dismissAdapterInfoModal: () => dispatch(createDismissAdapterInfoModal()),
    onAddNewAdapter: () => dispatch(createOnAddNewAdapter()),
    createNewAdapter: (adapter: BaseAdapterModel) =>
      dispatch(adapterActions.create.request(adapter)),
  };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
export default compose(withReducer, withConnect)(AdaptersPage);



