/**
 *
 * AdapterCard
 *
 */

import React, { SFC } from 'react';
// import styled from 'styled-components';
import { AdapterModel } from 'cb-utils/console-entity-models';
import { withRouter } from 'react-router';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import Desc from 'components/CardDescription';
import NavCard from 'components/NavToDetailCard';

interface AdapterCardProps extends SystemRouteProps<{}> {
  adapter: AdapterModel;
}

const AdapterCard: SFC<AdapterCardProps> = (props) => {
  const adapterName = props.adapter.name;
  return (
    <NavCard name={adapterName}>
      <div>{adapterName}</div>
      <div>
        <Desc>{props.adapter.description}</Desc>
      </div>
    </NavCard>
  );
};

export default withRouter(AdapterCard);



