/*
 *
 * AdaptersPage reducer
 *
 */

// import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';

export interface State {
  [key: string]: string;
}

export const initialState = {};

function adaptersPageReducer(state: State = initialState, action: ActionTypes) {
  switch (action.type) {
    case ActionConstants.DEFAULT_ACTION:
      return state;
    default:
      return state;
  }
}

export default adaptersPageReducer;



