/**
 *
 * AdapterInfoModal
 *
 */

import React from 'react';
import styled from 'styled-components';
import { Modal, ModalFooter, ModalBody, Button } from 'reactstrap';
import { FormattedMessage, injectIntl } from 'react-intl';
import messages from './messages';
import Yup from 'yup';
import { BaseAdapterModel, AdapterModel } from 'cb-utils/console-entity-models';
import consoleMessages from 'containers/Console/messages';
import { OPTION_TYPE, TEXT_AREA_TYPE } from 'containers/Portal/constants';
import CbModalHeader from 'components/CbModalHeader';
import { ARCH_OPTIONS } from 'components/EdgeSetup';
import CbButton from 'components/CbButton';
import { Formik, Form, Field, FormikProps } from 'formik';
import FormikInputWrapper from 'components/FormikInputWrapper';
import { assetNameValidations } from 'utils/validations';

interface AdapterInfoModalProps {
  toggle: () => void;
  submit: (adapter: BaseAdapterModel | AdapterModel) => void;
  isSubmitting: boolean;
  error: string;
  name?: string;
  description?: string;
  architecture?: string;
}

interface AdapterInfoModalState {
  adapter: BaseAdapterModel;
}

interface Values {
  name: string;
  architecture: string;
  description: string;
}

const ButtonWrapper = styled(Button)`
  margin-right: 0.5rem;
`;

const Spacer = styled.div`
  margin: 0 5px;
`;

class AdapterInfoModal extends React.Component<
  AdapterInfoModalProps & ReactIntl.InjectedIntlProps,
  AdapterInfoModalState
> {
  // eslint-disable-line react/prefer-stateless-function

  state: AdapterInfoModalState = {
    adapter: {
      name: this.props.name ? this.props.name : '',
      description: this.props.description ? this.props.description : '',
      architecture: this.props.architecture ? this.props.architecture : '',
    },
  };

  render() {
    return (
      <Formik
        enableReinitialize
        validationSchema={Yup.object().shape({
          name: assetNameValidations(this.props.intl),
        })}
        initialValues={{
          name: this.props.name ? this.props.name : '',
          architecture: this.props.architecture ? this.props.architecture : '',
          description: this.props.description ? this.props.description : '',
        }}
        onSubmit={(values: Values) => {
          this.props.submit({
            name: values.name,
            architecture: values.architecture,
            description: values.description,
          });
        }}
        render={({
          values,
          touched,
          errors,
          dirty,
          handleChange,
          setFieldValue,
        }: FormikProps<Values>) => (
          <Modal isOpen>
            <div data-cy="adapter-info-body">
              <CbModalHeader
                title={
                  this.props.name ? (
                    <FormattedMessage {...messages.editAdapter} />
                  ) : (
                    <FormattedMessage {...messages.newAdapter} />
                  )
                }
                subtitle="Adapter"
                toggle={this.props.toggle}
              />
              <Form>
                <ModalBody>
                  <Field
                    autoFocus={true}
                    readOnly={this.props.name ? true : false}
                    name="name"
                    label={<FormattedMessage {...consoleMessages.name} />}
                    component={FormikInputWrapper}
                  />
                  <Spacer />
                  <Field
                    name="architecture"
                    label={
                      <FormattedMessage {...consoleMessages.architecture} />
                    }
                    type={OPTION_TYPE}
                    options={[...ARCH_OPTIONS]}
                    component={FormikInputWrapper}
                  />
                  <Spacer />
                  <Field
                    name="description"
                    label={
                      <FormattedMessage {...consoleMessages.description} />
                    }
                    type={TEXT_AREA_TYPE}
                    component={FormikInputWrapper}
                  />
                  <Spacer />
                </ModalBody>
                <ModalFooter>
                  <div data-cy="adapter-info-footer">
                    <ButtonWrapper onClick={this.props.toggle}>
                      <FormattedMessage {...messages.cancel} />
                    </ButtonWrapper>
                    <CbButton
                      id="adapter-submit"
                      {...{ errors, touched }}
                      color="primary"
                      isLoading={this.props.isSubmitting}
                    >
                      {this.props.name ? (
                        <FormattedMessage {...messages.editAdapter} />
                      ) : (
                        <FormattedMessage {...messages.newAdapter} />
                      )}
                    </CbButton>
                  </div>
                </ModalFooter>
              </Form>
            </div>
          </Modal>
        )}
      />
    );
  }
}

export default injectIntl(AdapterInfoModal);



